import {
		mapState, 
    } from 'vuex';
import md5 from '../../kayida/common/md5.js'

export default {
	computed: {
	            ...mapState(['userInfo'])  
	        },
	data() {
		return {
			time:0,
			times:'',
			mobile:'',
			code:'',
			idcard:'',
			realname:'',
			bank_card:'',
		}
	},
	methods: {
		realGetAuth(){
				let data = {
					mobile:this.mobile,
					send_type:'identity_check'
				}
				this.request.httpRequest('api/sms/sendCode',data,'POST').then(res => {
					if (res.data.code === 1000){
						uni.showToast({
							title: res.data.msg,
							icon: "none"
						})
						this.time = 1;
						this.times = 59;
						setInterval(()=>{
							this.times --;
							if(this.times < 1){
								this.time = 2;
								clearInterval()
							}
						},1000)
					}
					if (res.data.code === 1100){
						uni.showToast({
							title: res.data.msg,
							icon: "none"
						})
					}
				})
		},
		realName() {
			let data = {
				mobile: this.mobile,
				code:this.code,
				idcard:this.idcard,
				realname:this.realname,
				bank_card:this.bank_card,
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/user/auth',data,'GET').then(res => {
				if (res.data.code === 1000){
					uni.showToast({
						title: "提交成功",
						icon: "success"
					})
					setTimeout(function() {
						uni.switchTab({
							url:"../main/main"
						})
					}, 1000);
				}
				if (res.data.code === 1100){
					uni.showToast({
						title: res.data.msg,
						icon: "none"
					})
				}
			})
		}
	}
}
